@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;500;600&family=Poppins&display=swap');

*{
  margin : 0 ;
  padding : 0 ;
  border : 0 ;
  outline : 0 ;
  box-sizing : border - box ;
  list-style : none ;
  text-decoration : none ;
 }

 html{
  scroll-behavior: smooth;
}

/* rgb(250, 236, 126); */
body{
  margin : 0 ;
  padding : 0 ;
  font-family: 'Nunito', sans-serif;
  background: #F0F2F5;
  color: var(--color-white);
  line-height: 1.7;
  overflow-x: hidden;
  
  /* background-image: url(../src/assets/bg4_texture.png); */
}
h1,h2,h3,h4,h5,p,small,li{
  cursor: default;
}